function convertedDataFromExcelForLineChart(IMRfromExcel) {
  let convertedIMR= [];
  let teamArray = [];
  
  for (let i = 1;  i <= IMRfromExcel.length; i++) {
    teamArray.push(IMRfromExcel[i-1])
  
    if (i % 3 === 0 && i !== 0) {
      convertedIMR.push(teamArray);
      teamArray = [];
    }
  };

  return convertedIMR.map( item => {
    return {
      name: item[0]['Country Name'],
      data: [
        {
          name: '2010',
          lower: item[0]['2010'],
          median: item[1]['2010'],
          upper: item[2]['2010'],
        }, 
        {
          name: '2011',
          lower: item[0]['2011'],
          median: item[1]['2011'],
          upper: item[2]['2011'],
        }, 
        {
          name: '2012',
          lower: item[0]['2012'],
          median: item[1]['2012'],
          upper: item[2]['2012'],
        }, {
          name: '2013',
          lower: item[0]['2013'],
          median: item[1]['2013'],
          upper: item[2]['2013'],
        }, {
          name: '2014',
          lower: item[0]['2014'],
          median: item[1]['2014'],
          upper: item[2]['2014'],
        }, {
          name: '2015',
          lower: item[0]['2015'],
          median: item[1]['2015'],
          upper: item[2]['2015'],
        }, {
          name: '2016',
          lower: item[0]['2016'],
          median: item[1]['2016'],
          upper: item[2]['2016'],
        }, {
          name: '2017',
          lower: item[0]['2017'],
          median: item[1]['2017'],
          upper: item[2]['2017'],
        }, 
  
      ]
    }
  });
}

export default convertedDataFromExcelForLineChart(IMRfromExcel)


